const fonts = [
    {
        name: "abc1",
        path: "./images/abc1/images"
    },
    {
        name: "abc2",
        path: "./images/abc2/images"
    },
    {
        name: "abc3",
        path: "./images/abc3/images"
    },
    {
        name: "abc4",
        path: "./images/abc4/images"
    },
];

export default class AbcApi {
    static getFonts () {
        return fonts;
    }
}



